body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html,
body {
  margin: 0;
}

body {
  /*background-color: #eeeeee;*/
  background-color: #efefef;
  /*background-color: #000000;*/
  font-family: 'Raleway', sans-serif;
}

#header {
  background-color: #efefef;
  /*background-color: #0c0c0c;*/
  height: 100vh;
  z-index: -5;
  border-bottom: 1px #0c0c0c solid;
}

