} */

.main-title {
  position: absolute;
  color: #0c0c0c;
  top: 40vh;
  left: 25vw;
  font-size: 36px;
  font-weight: 200;
  z-index: 10;
}

.photo {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 100vh;
}

#main-menu {
  /*background: #0c0c0c;*/
  background: #efefef;
  display: -webkit-flex;
  display: flex;
  height: 50px;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  position: fixed;
  top: 0px;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  width: 100%;
  z-index: 2;
}

.menu-links {
  color: #0c0c0c;
  /*font-family: franklin-gothic-urw, sans-serif;*/
  /*font-size: 12px;*/
  font-weight: 400px;
  /*font-family: 'Titillium Web', sans-serif;*/
  padding: 15px 7.5%;
  text-decoration: none;
  /* z-index: 11; */
}

.menu-links:hover {
  color: #777;
  -webkit-transition-duration: 0.25s;
          transition-duration: 0.25s;
}

#main-menu-social {
  display: none;
}

h2 {
  color: #0c0c0c;
  margin: 0;
  padding: 50px 0 50px;
  background-color: #efefef;
  font-weight: 200;
  font-size: 36px;
  text-align: center;
  width: 100%;
  /* border-top: 1px white solid; */
}

#tour {
  background-image: url(/kate-dinsmore/static/media/kieldrum.373bbea1.jpg);
  height: 850px;
  margin: 0;
}

#tour-date-container {
  margin: 0 5% 100px;
}

#bio {
  /*background-image: url("images/albuminsert.jpg");*/
  height: 850px;
  margin: 0 auto;
}

.bio-photo {
  position: absolute;
  /*right: 0;*/
  /*bottom: 0;*/
  height: 100vh;
}

.bio-text {
  color: #0c0c0c;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 200;
  line-height: 1.75;
  margin: 0 5%;
  padding: 100px 5% 0;
  text-align: center;
}

#lets-rage {
  color: #0c0c0c;
  font-family: 'Titillium Web', sans-serif;
  font-size: 20px;
  font-weight: 200;
  line-height: 1.75;
  margin: 0 auto;
  padding: 120px 5% 0;;
  text-align: center;
  /*position: absolute;*/
  /*bottom: 20px;*/
}

/*#music-title {
  color: #0c0c0c;
  background-color: #FFFFFF;
}*/

#music {
  background-image: url(/kate-dinsmore/static/media/albuminsert.cc02fd64.jpg);
  height: 850px;
  margin: 0;
  text-align: center;
  border-top: 1px #0c0c0c solid;
}

#music-spacer {
  height: 100px;
}

#music-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 200px auto;
}

.hidden-tunes, .hidden-tunes-2, .hidden-tunes-3 {
  display: none;
}

.music-text {
  margin-top: 60px;
}

.music-links {
  color: #0c0c0c;
  font-family: 'Titillium Web', sans-serif;
  font-size: 26px;
  margin-top: 20px;
  text-decoration: none;
}

#contact {
  background-color: #0c0c0c;
  padding: 100px 0 100px;
  text-align: center;
  width: 100%;
}

#contact-title {
  color: #efefef;
  background-color: #0c0c0c;
  padding-top: 0;
  padding-bottom: 20px;
}

.contact-text {
  color: #efefef;
  padding: 0 0 0 6%;
  font-size: 20px;
  font-weight: 200;
  line-height: 1.7;
  width: 90%;
}

.contact-links {
  font-weight: 400;
  color: #fff;
  text-decoration: none;
}

.parallax-scrolling {
  margin: 0;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#social-icon-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  width: 320px;
}

.social-icon {
  margin: 30px 15px;
  width: 60px;
  /*transition-duration: 0.05s;*/
}

.social-icon:hover {
  opacity: 0.8;
  /*transition-duration: 0.05s;*/
}

@media screen and (min-width: 500px) {
    #main-menu {
      height: 50px;
      display: -webkit-flex;
      display: flex;
      margin: 0 auto;
      position: fixed;
      top: 0px;
      width: 100%;
    }

    .montu-logo {
      top: 20vh;
      left: 50%;
      margin-left: -200px;
      width: 400px;
    }

    .music-text {
      margin-top: 20px;
    }

    .social-icon {
      margin: 30px 20px;
      width: 75px;
    }

    /*@keyframes menu-background {
      0% {background-color: #A3ADB8};
      30% {background-color: #83929F};
      60% {background-color: #4E6172};
      90% {background-color: #A3ADB8};
    }*/

}

@media screen and (min-width: 800px) {
  .montu-logo {
    margin-left: -300px;
    width: 600px;
  }

  #main-menu-social {
    display: inline-block;
    padding: 15px;
    position: fixed;
    right: 25px;
    z-index: 12;
  }

  .menu-social-icon {
    margin: 0 5px;
    height: 20px;
    -webkit-transition-duration: 0.25s;
            transition-duration: 0.25s;
    z-index: 12;
  }

  .menu-social-icon:hover {
    opacity: 0.5;
    -webkit-transition-duration: 0.25s;
            transition-duration: 0.25s;
  }

  .bio-text{
    margin: 0 15%;
    font-size: 20px;
  }

  #lets-rage {
    font-size: 24px;
    padding-top: 100px;
    position: static;
  }

  .hidden-tunes {
    display: inline-block;
  }

  social-icon-container {
    width: 600px;
  }
}

