@media screen and (max-width: 400px) {
  .menu-links {
    font-size: 13px;
  }

  .social-icon-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }

  .social-icon {
    height: 40px;
  }

  #bio {
    height: 1000px;
  }

  .bio-text {
    padding-top: 30px;
  }
}

@media screen and (max-width: 1300px) {
  .main-title {
    bottom: 20vh;
    left: 60vw;
    color: #efefef;
  }
